// @import url(./Common.css);
@import url(./_Common.scss);
@import url(./font.css);
@import url(./reset.css);
// common.css
@mixin mainCateBtn($url) {
  width: 40px;
  height: 40px;
  border-radius: 15px;
  background: url($url) no-repeat center/cover;
  @content;
}
@mixin sideBtn() {
  width: 15%;
  height: 30px;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  cursor: pointer;
  border: 1px solid rgba(164, 164, 164, 0.5);
  color: rgb(42, 42, 42);
  @content;
}

$grayBg: rgba(164, 164, 164, 0.5);
$color1: #fbcbc3;
$color3: #eb735e;
// $color1: #87daec;
$color2: #feb2b2;
// $color3: #46cae8;

$mainIconUrl: (
  1: "../../public/assets/images/ico_exhibition.png",
  4: "../../public/assets/images/ico_concert.png",
  5: "../../public/assets/images/ico_musical.png",
  6: "../../public/assets/images/ico_k-music.png",
  7: "../../public/assets/images/ico_theater.png",
  8: "../../public/assets/images/ico_education.png",
  2: "../../public/assets/images/ico_dance.png",
  3: "../../public/assets/images/ico_classic.png",
  10: "../../public/assets/images/ico_festival.png",
  11: "../../public/assets/images/ico_instrument.png",
  9: "../../public/assets/images/ico_movies.png",
  12: "../../public/assets/images/ico_etc.png",
);
html {
  scroll-behavior: smooth;
}
body {
  &::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera*/
  }
}
// header css
.bg-layer {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100vh;
  background: url("../../public/assets/images/bg4-edited.jpg") no-repeat center/cover;
  filter: grayscale(0.3);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr 1fr;
  column-gap: 480px;
  transition: gap 0.3s ease-in-out;
  &.active {
    column-gap: 50vw;
  }
  .side-contents {
    justify-self: center;
    align-self: flex-end;
    color: white;
    padding: 0 15px;
    .side-contents-inner {
      .side-contents-text {
        letter-spacing: 0.1em;
        text-align: center;
        h2 {
          margin-bottom: 30px;
          font-size: 5vw;
        }
        p:nth-of-type(1) {
          font-size: 1.5vw;
          margin-bottom: 10px;
        }
        p:nth-of-type(2) {
          font-size: 1.1vw;
        }
      }
    }
  }
  .side-contentsR {
    grid-column-start: 1;
    justify-self: center;
    align-self: center;
    color: white;
    width: 100px;
    height: 63px;
    background-color: rgba(255, 255, 255, 0.4);
    display: flex;
    border-radius: 50px;
    padding: 6px;
    cursor: pointer;
    &[data-ison="true"] {
      justify-content: flex-end;
    }
    // .exmaple-container {
    //   width: 100px;
    //   height: 100px;
    //   background-color: #fff;
    //   z-index: 100;
    // }
    .handle {
      width: 50px;
      height: 50px;
      background-color: #fff;
      border-radius: 40px;
      border: none;
      outline-style: none;
      cursor: pointer;
    }
  }
}
.content-wrapper {
  position: relative;
  max-width: 480px;
  min-height: 100vh;
  background-color: #fff;
  margin: 0 auto;
  box-shadow: 0px 0px 15px 6px rgba(255, 255, 255, 0.3);
  transition: all 0.3s ease-in-out;
  &.active {
    max-width: 50vw;
  }
  .header {
    z-index: 10;
    position: sticky;
    top: 0;
    left: 0;
    // recommend background color that is suitable for the theme
    margin-bottom: 15px;
    .header-container {
      &::after {
        content: "";
        display: block;
        border-bottom: 1px solid rgb(234, 234, 234);
      }
      .header-inner {
        display: grid;
        grid-template-columns: 2fr 1fr;
        grid-auto-rows: minmax(60px, auto);
        place-items: center;
        gap: 30%;
        background-color: #fff;
        .logo {
          justify-self: left;
          align-self: end;
          figure {
            img {
              width: 100%;
              padding: 5% 7%;
            }
          }
        }
        .account-btn {
          position: relative;
          justify-self: end;
          align-self: center;
          padding: 0 15% 0 0;
          z-index: 1;
          .profile-img {
            cursor: pointer;
            img {
              width: 50px;
              height: 50px;
              border-radius: 50%;
              object-fit: cover;
            }
            div {
              width: 50px;
              height: 50px;
              display: flex;
              justify-content: center;
              align-items: center;
              svg {
                z-index: -1;
              }
            }
          }
          .profile-select {
            width: 300px;
            position: absolute;
            margin: 10px 5px 0 0;
            border-radius: 10px;
            right: 0;
            text-align: center;
            opacity: 0;
            user-select: none;
            pointer-events: none;
            &.active {
              opacity: 1;
              transition: opacity 0.25s ease-in-out;
              user-select: auto;
              pointer-events: auto;
              background-color: #f6f6f6;
            }
            .user-on {
              width: 100%;
              display: grid;
              grid-template-columns: 1.5fr 2fr 1.5fr;
              border: 1px solid #ddd;
              border-radius: 10px;
              li {
                padding: 10px 0;
                cursor: pointer;
                img {
                  width: 50px;
                  height: 50px;
                  border-radius: 50%;
                  object-fit: cover;
                  cursor: auto;
                }
              }
              li:nth-child(2) {
                width: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: flex-start;
                line-height: 1.2rem;
                font-size: 0.8rem;
                h2 {
                  letter-spacing: 0.05rem;
                  cursor: auto;
                }
                P {
                  font-size: 0.7rem;
                  cursor: auto;
                }
                a {
                  color: #4a7ecf;
                  text-decoration: underline;
                }
              }
              li:nth-child(3) {
                justify-self: center;
                align-self: center;
                p {
                  font-size: 0.7rem;
                  padding: 5px 10px;
                  border: 1px solid rgba(164, 164, 164, 0.5);
                }
              }
              > a {
                grid-column: 1/4;
                background-color: #faf7f7;
                border-top: 1px solid #ddd;
                border-radius: 0 0 10px 10px;
                font-size: 0.8rem;
                li {
                  // 목록이 더 생기면 수정
                }
              }
            }
            .user-none {
              li {
                padding: 10px 0;
                cursor: pointer;
                border: 1px solid #ddd;
                border-radius: 10px;
              }
            }
          }
        }
      }
    }
  }
  .main-container {
    min-height: calc(100vh - 135px);
    // top button css
    .to-top {
      position: fixed;
      top: 85vh;
      right: 50vw;
      transform: translateX(50%);
      width: 35px;
      height: 35px;
      border-radius: 50%;
      box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.8);
      color: #000;
      background-color: rgba(255, 255, 255, 0.8);
      font-size: 1.25em;
      border: none;
      justify-content: center;
      align-items: center;
      z-index: 100;
      opacity: 0;
      pointer-events: none;
      transition: opacity 0.25s ease-in-out;
      &.active {
        opacity: 1;
        cursor: pointer;
        pointer-events: auto;
      }
    }
    // index css
    .container1 {
      position: relative;

      &::after {
        content: "";
        margin: 0 0 15px 0;
        display: block;
        border-bottom: 5px solid $grayBg;
      }
      .swiper-title {
        text-align: center;
        margin-bottom: 15px;
        h2 {
          font-size: 1.25em;
          margin-bottom: 10px;
        }
        p {
          font-size: 0.75em;
          color: rgb(118, 118, 118);
        }
        .recommend {
          cursor: pointer;
          position: absolute;
          top: 20px;
          right: 10px;
          width: 80px;
          height: 20px;
          background-color: white;
          color: #000;
          border: 1px solid #fff;
          border-radius: 25px;
          z-index: 11;
        }
      }
      .swiper {
        width: 100%;
        height: 50vh;
        .swiper-slide {
          width: 85%;
          border-radius: 15px;
          text-align: center;
          font-size: 18px;
          background-color: #ddd;
          display: flex;
          justify-content: center;
          align-items: center;
        }
        a {
          width: 100%;
          height: 100%;
          p {
            display: none;
          }
          h2 {
            display: none;
          }
          img {
            border-radius: 15px;
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
        .swiper-pagination-bullet {
          margin: 0 3px;
        }
        .swiper-pagination-bullet-active {
          background-color: #000;
        }
      }

      .info-links {
        padding: 0 10px;
        display: flex;
        justify-content: space-evenly;
        margin-top: 15px;
        text-align: center;
        flex-wrap: wrap;
        figure {
          width: 16.66%;
          margin-bottom: 15px;
          figcaption {
            font-weight: 600;
            margin-top: 5px;
            font-size: 0.75em;
          }
          .box {
            display: inline-block;
            border-radius: 15px;
            &:hover {
              background-color: $color1;
            }
            button {
              cursor: pointer;
              border: 1px solid rgba(164, 164, 164);
              p {
                display: none;
              }
            }
          }
          @each $i, $name in $mainIconUrl {
            &:nth-child(#{$i}) button {
              @include mainCateBtn($name);
            }
          }
        }
      }
    }
    .container2 {
      &::after {
        content: "";
        margin: 15px 0 15px 0;
        display: block;
        border-bottom: 5px solid rgba(164, 164, 164, 0.3);
      }
      .place-links {
        h2 {
          margin: 0 0 15px 15px;
        }
        .swiper {
          width: 95%;
          height: 10vh;
          .swiper-slide {
            width: 25%;
            text-align: center;
            font-size: 1em;
            display: flex;
            justify-content: center;
            align-items: flex-end;
            figure {
              position: relative;
              width: 100%;
              height: 100%;
              overflow: hidden;
              border-radius: 15px;
              &:hover img {
                filter: brightness(1);
                scale: 1.1;
              }
              &:hover figcaption {
                font-size: 1.25rem;
                text-shadow: 0px 0px 10px rgba(0, 0, 0, 1);
              }
              img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                filter: brightness(0.6);
                transition: all 0.25s ease-in-out;
              }
              figcaption {
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                padding: 10px;
                display: flex;
                font-weight: bold;
                justify-content: center;
                align-items: flex-end;
                color: #fff;
                transition: all 0.25s ease-in-out;
              }
            }
          }
        }
      }
    }
    .container3 {
      &::after {
        content: "";
        margin: 15px 0 0 0;
        display: block;
        border-bottom: 5px solid rgba(164, 164, 164, 0.3);
      }
      .date-links {
        // margin-bottom: 75px;
        h2 {
          margin: 0 0 15px 15px;
        }
        .swiper {
          width: 95%;
          height: 10vh;
          .swiper-slide {
            width: 15%;
            height: calc(100% - 3px);
            border-radius: 15px;
            text-align: center;
            font-size: 18px;
            display: flex;
            flex-direction: column;
            background-color: rgb(225, 225, 225);
            justify-content: space-evenly;
            align-items: center;
            padding: 5px;
            transition: all 0.25s ease-in-out;
            &:hover {
              filter: drop-shadow(3px 3px 0px rgb(200, 200, 200));
            }
            &:first-child {
              background-color: $color1;
            }
            div {
              width: 80%;
              height: 100%;
              a {
                display: flex;
                flex-direction: column;
                justify-content: space-evenly;
                height: 100%;
                p {
                  font-weight: 600;
                  &.colorBlue {
                    color: blue;
                  }
                  &.colorRed {
                    color: red;
                  }
                }
                p:nth-child(1) {
                  font-size: 0.7125em;
                }
                p:nth-child(2) {
                  padding: 5px;
                  border-radius: 7.5px;
                  background-color: #fff;
                  font-size: 0.65em;
                }
              }
            }
          }
        }
      }
    }
    // search css
    .search-list {
      position: relative;
      width: 100%;
      height: 100%;
      .search-header {
        position: sticky;
        top: 60px;
        left: 0;
        background-color: #fff;
        .search-bar {
          text-align: center;
          margin-bottom: 15px;
          display: flex;
          justify-content: space-evenly;
          div {
            width: 70%;
            input {
              width: 100%;
              height: 30px;
              border: 1px solid rgba(164, 164, 164, 0.5);
              border-radius: 15px;
              padding: 0 10px;
              text-align: center;
            }
            .list-wrapper {
              height: 20vh;
              position: absolute;
              overflow: scroll;
              display: none;
              &::-webkit-scrollbar {
                display: none; /* Chrome, Safari, Opera*/
              }
              &.active {
                display: block;
              }
              ul {
                z-index: 2;
                width: 100%;
                background-color: rgba(255, 255, 255, 0.9);
                // border-left: 1px solid rgba(164, 164, 164, 0.5);
                // border-right: 1px solid rgba(164, 164, 164, 0.5);
                li {
                  margin: 0;
                  overflow: hidden;
                  white-space: nowrap;
                  text-overflow: ellipsis;
                  text-align-last: left;
                  font-size: 0.85em;
                  padding: 10px 5px;
                  border-bottom: 1px solid rgba(164, 164, 164, 0.5);
                  cursor: pointer;
                  &:hover {
                    background-color: $color1;
                  }
                  &.active {
                    background-color: $color1;
                  }
                }
              }
            }
          }
          #search-by-place {
            display: none;
          }
          .reset-btn {
            @include sideBtn();
            &:hover {
              background-color: $color1;
            }
          }
        }
        .search-place {
          width: 100%;
          height: 40px;
          background-color: rgba(164, 164, 164, 0.2);
          margin-bottom: 15px;
          text-align: center;
          display: flex;
          justify-content: space-around;
          align-items: center;
          input[type="date"] {
            width: 20%;
            height: 30px;
            border: none;
            border-radius: 15px;
            text-align: center;
            color: rgba(0, 0, 0, 0);
            background: white url(https://cdn3.iconfinder.com/data/icons/linecons-free-vector-icons-pack/32/calendar-16.png) no-repeat center center;
            &:hover {
              background-color: $color1;
            }
            &::-webkit-inner-spin-button,
            &::-webkit-calendar-picker-indicator {
              width: 100%;
              height: 100%;
              opacity: 0;
              cursor: pointer;
            }
            &::-webkit-datetime-edit-text,
            &::-webkit-datetime-edit-month-field,
            &::-webkit-datetime-edit-day-field,
            &::-webkit-datetime-edit-year-field {
              display: none;
            }
          }
          .selected-date {
            font-size: 0.85em;
            color: #000;
            font-weight: 600;
            // transition: steps(1) 0.25s ease-in-out;
          }
          #search-by-place {
            cursor: pointer;
            width: 20%;
            height: 70%;
            border-radius: 15px;
            display: flex;
            text-align: center;
            // &:hover {
            //   background-color: $color1;
            // }
          }
        }
        .search-cate {
          .swiper {
            width: 100%;
            height: 100%;
            margin-bottom: 15px;
            padding: 0 15px 0 15px;
            .swiper-slide {
              width: 25%;
              text-align: center;
              font-size: 1em;
              display: flex;
              justify-content: center;
              align-items: center;
              button {
                width: 95%;
                font-size: 0.75em;
                border-radius: 15px;
                border: none;
                background-color: rgb(240, 240, 240);
                padding: 5px 10px;
                cursor: pointer;
                &:hover {
                  background: $color1;
                  transition: background-color 0.25s ease-in-out;
                }
                &.selected {
                  background: $color1;
                }
              }
              // 왼쪽부터 채우기
              // transition: background 0.25s ease-in-out;
              // background: linear-gradient(to right, $color1 50%, rgba(240, 240, 240) 50%);
              // background-size: 200% 100%;
              // background-position: right bottom;
              // &:hover {
              //   background-position: left bottom;
              // }
            }
          }
        }
      }
      .main-list {
        &:last-child .list-wrapper {
          padding-bottom: 75px;
        }
        .list-wrapper {
          display: grid;
          grid-template-columns: 1fr 3fr;
          margin: 0 10px;
          &::after {
            content: "";
            display: block;
            border-bottom: 1px solid rgb(234, 234, 234);
            margin: 7.45px 0;
            grid-column: 1 / 3;
          }
          a {
            .list-image {
              width: 100%;
              img {
                width: 100%;
              }
            }
          }
          .list-contents {
            width: 100%;
            display: grid;
            padding: 0 0 0 10px;
            svg {
              fill: #fff;
              cursor: pointer;
              path {
                transition: all 0.25s ease-in-out;
                &.active {
                  fill: #f00;
                  stroke: none;
                }
              }
            }
            h2 {
              font-size: 1em;
              margin-bottom: 5px;
            }
            p {
              align-self: center;
              margin-bottom: 3px;
              font-size: 0.825em;
              &:last-child {
                text-align: right;
                font-weight: 600;
                margin: 0;
              }
            }
          }
        }
      }
      .loading-data {
        height: 5vh;
        text-align: center;
      }
    }
    // map css
    .map_wrap,
    .map_wrap * {
      margin: 0;
      padding: 0;
      font-size: 12px;
    }
    .map_wrap {
      position: relative;
      width: 100%;
      height: calc(100vh - 135px);
      #map {
        position: relative;
        width: 100%;
        min-height: 100%;
      }
      #category {
        position: absolute;
        top: 10px;
        left: 10px;
        border-radius: 5px;
        border: 1px solid #909090;
        box-shadow: 0 1px 1px rgba(0, 0, 0, 0.4);
        background: #fff;
        overflow: hidden;
        z-index: 2;
        li {
          float: left;
          list-style: none;
          width: 50px;
          border-right: 1px solid #acacac;
          padding: 6px 0;
          text-align: center;
          cursor: pointer;
          &:hover {
            background: $color1;
            transition: background-color 0.25s ease-in-out;
          }
          &.on {
            background: $color1;
            border-left: 1px solid #acacac;
            margin-left: -1px;
          }
          // &:hover {
          // }
          &:last-child {
            margin-right: 0;
            border-right: 0;
          }
          span {
            display: block;
            margin: 0 auto 3px;
            width: 27px;
            height: 28px;
          }
          .category_bg {
            background: url("../../public/assets/images/spritesheet2.png") no-repeat center/cover;
          }
          .bank {
            background-position: 0 0;
          }
          .car-park {
            background-position: -28px 0;
          }
          .train {
            background-position: -56px 0;
          }
          .restaurant {
            background-position: -84px 0;
          }
          .cafe {
            background-position: -112px 0;
          }
          .store {
            background-position: -140px 0;
          }
          &.on .category_bg {
            // background-position-x: -46px;
          }
        }
      }
      .placeinfo_wrap {
        position: absolute;
        bottom: 28px;
        left: -150px;
        width: 300px;
        &.after {
          content: "";
          position: relative;
          margin-left: -12px;
          left: 50%;
          width: 22px;
          height: 12px;
          background: url("https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/vertex_white.png");
        }
        .placeinfo {
          position: relative;
          width: 100%;
          border-radius: 6px;
          border: 1px solid #ccc;
          border-bottom: 2px solid #ddd;
          padding-bottom: 10px;
          background: #fff;
          &:nth-of-type(n) {
            border: 0;
            box-shadow: 0px 1px 2px #888;
          }
          a,
          a:hover,
          a:active {
            color: #fff;
            text-decoration: none;
          }
          a,
          span {
            display: block;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
          }
          span {
            margin: 5px 5px 0 5px;
            cursor: default;
            font-size: 13px;
          }
          .title {
            font-weight: bold;
            font-size: 14px;
            border-radius: 6px 6px 0 0;
            margin: -1px -1px 0 -1px;
            padding: 10px;
            color: #fff;
            background: #d95050;
            background: #d95050 url("https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/arrow_white.png") no-repeat right 14px center;
          }
          .tel {
            color: #0f7833;
          }
          .jibun {
            color: #999;
            font-size: 11px;
            margin-top: 0;
          }
        }
      }
      #loc-btn {
        position: absolute;
        bottom: 50px;
        right: 50px;
        z-index: 2;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background: #fff;
        border: 1px solid #ccc;
        box-shadow: 0 1px 1px rgba(0, 0, 0, 0.4);
        display: flex;
        justify-content: center;
        align-items: center;
        #btn-loc {
          cursor: pointer;
          width: 40px;
          height: 40px;
          border: 0;
          background: url("../../public/assets/images/myLoc.png") no-repeat center/cover;
        }
      }
    }
    // recommend css
    .recommends {
      width: 100%;
      .recommends-wrapper {
        text-align: center;
        > div {
          width: 100%;
          &::after {
            content: "";
            margin: 0 auto;
            margin-bottom: 15px;
            width: 90%;
            display: block;
            border-bottom: 1px solid $grayBg;
          }
          a {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            overflow: hidden;
            margin: 0 15px 15px;
            border-radius: 15px;
            &:hover img {
              filter: brightness(1);
              scale: 1.03;
              transition: filter 0.5s, scale 0.5s;
            }
            &:hover .text-box {
              opacity: 0;
              transition: opacity 0.5s;
              user-select: none;
            }
            .text-box {
              position: absolute;
              color: #fff;
              line-height: 1.5em;
              z-index: 1;
              padding: 25px;
              p:first-child {
                font-size: 1.25em;
                margin-bottom: 15px;
              }
              p:nth-child(2) {
                font-size: 1em;
                margin-bottom: 50px;
              }
              h2 {
                font-size: 1.5em;
              }
            }
            img {
              filter: brightness(0.3);
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
          }
        }
      }
    }
    // cultural css
    .cultural-space {
      h1 {
        text-align: center;
        // margin-bottom: 15px;
      }
      .cultural-space-wrapper {
        .cultural-header {
          position: sticky;
          top: 60px;
          left: 0;
          background-color: #fff;
          .search-bar {
            text-align: center;
            margin-bottom: 15px;
            display: flex;
            justify-content: space-evenly;
            div {
              position: relative;
              width: 60%;
              input {
                width: 100%;
                height: 30px;
                border: 1px solid rgba(164, 164, 164, 0.5);
                border-radius: 15px;
                padding: 0 10px;
                text-align: center;
              }
              .list-wrapper {
                width: 100%;
                height: 20vh;
                position: absolute;
                overflow: scroll;
                display: none;
                &::-webkit-scrollbar {
                  display: none; /* Chrome, Safari, Opera*/
                }
                &.active {
                  display: block;
                }
                ul {
                  z-index: 2;
                  width: 100%;
                  background-color: rgba(255, 255, 255, 0.9);
                  border-left: 1px solid rgba(164, 164, 164, 0.5);
                  border-right: 1px solid rgba(164, 164, 164, 0.5);
                  li {
                    margin: 0;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    text-align-last: left;
                    font-size: 0.85em;
                    padding: 10px 5px;
                    border-bottom: 1px solid rgba(164, 164, 164, 0.5);
                    cursor: pointer;
                    &:hover {
                      background-color: $color1;
                    }
                    &.active {
                      background-color: $color1;
                    }
                  }
                }
              }
            }
            #search-by-place {
              @include sideBtn();
              text-align: center;
              // &:hover {
              //   background-color: $color1;
              // }
            }
            .reset-btn {
              @include sideBtn();
              &:hover {
                background-color: $color1;
              }
            }
          }
          .search-cate {
            padding-top: 15px;
            .swiper {
              width: 100%;
              height: 100%;
              margin-bottom: 15px;
              padding: 0 15px 0 15px;
              .swiper-slide {
                width: 25%;
                text-align: center;
                font-size: 1em;
                display: flex;
                justify-content: center;
                align-items: center;
                button {
                  width: 95%;
                  font-size: 0.75em;
                  border-radius: 15px;
                  border: none;
                  background-color: rgb(240, 240, 240);
                  padding: 5px 10px;
                  cursor: pointer;
                  &:hover {
                    background: $color1;
                    transition: background-color 0.25s ease-in-out;
                  }
                  &.selected {
                    background: $color1;
                  }
                }
              }
            }
          }
        }
        ul {
          .cultural-space-items {
            display: grid;
            grid-template-columns: 30% 1fr;
            gap: 5px 30px;
            margin: 0 10px;
            &::after {
              content: "";
              display: block;
              border-bottom: 1px solid rgb(234, 234, 234);
              margin: 15px 0;
              grid-column: 1/3;
            }
            &:last-child {
              margin-bottom: 75px;
            }
            figure {
              width: 100%;
              height: 70px;
              justify-self: center;
              // align-self: center;
              a {
                img {
                  border-radius: 15px;
                  width: 100%;
                  height: 100%;
                  object-fit: cover;
                }
              }
            }
          }
          .cultural-space-contents {
            width: 100%;
            display: grid;
            grid-template-columns: 3fr 1fr;
            grid-template-rows: repeat(2, 1fr);
            justify-self: center;
            align-self: center;
            gap: 2px;
            font-size: 0.85em;
            .text-wrapper {
              grid-row: 1/3;
              p:nth-child(1) {
                line-height: 1.125;
                &:first-child {
                  font-weight: bold;
                  font-size: 1.3em;
                  margin-bottom: 5px;
                }
                &:last-child {
                  font-size: 1em;
                }
              }
            }
            a {
              grid-column: 2/3;
              grid-row: 1/3;
              button {
                width: 100%;
                height: 100%;
                border: 0;
                background: none;
                cursor: pointer;
              }
            }
          }
          .see-more {
            width: 100%;
            grid-column: 1/3;
            text-align: center;
            &.active button:after {
              transform: rotate(180deg);
            }
            &.active div {
              transition: all 1s;
              max-height: 100vh;
            }
            button {
              width: 100%;
              background-color: transparent;
              color: #000;
              border: none;
              cursor: pointer;
              &:after {
                content: "";
                margin: 0 auto;
                display: block;
                background: url("../../public/assets/images/arrow_down.png") no-repeat center/cover;
                width: 15px;
                height: 15px;
              }
            }
            div {
              text-align: left;
              max-height: 0;
              overflow: hidden;
              // transition: all 0.1s;
              p {
                margin-top: 10px;
                line-height: 1.4em;
                font-size: 0.85em;
              }
            }
          }
        }
      }
    }
    // recent css
    .recent-liked {
      h2 {
        text-align: center;
        margin-bottom: 15px;
      }
      .like-list {
        &:last-child {
          margin-bottom: 75px;
        }
        li {
          margin: 0 10px;
          display: grid;
          grid-template-columns: 5fr 1fr;
          &::after {
            content: "";
            display: block;
            border-bottom: 1px solid rgb(234, 234, 234);
            margin: 10px 0;
            grid-column: 1/3;
          }
          div {
            align-self: center;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
          button {
            width: 100%;
            height: 30px;
            border-radius: 15px;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: #fff;
            cursor: pointer;
            border: 1px solid rgba(164, 164, 164, 0.5);
            color: rgb(42, 42, 42);
            &:hover {
              background-color: $color1;
            }
          }
        }
      }
    }
  }
  // footer css
  .footer {
    position: sticky;
    bottom: 0;
    text-align: center;
    font-size: 0.725em;
    z-index: 10;
    .tab-bar-container {
      width: 100%;
      height: 60px;
      background: white;
      .tab-bar-container {
        position: fixed;
        bottom: 0;
        width: 100%;
        height: 60px;
        background: white;
      }
      .tab-bar-inner {
        position: relative;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        box-shadow: 1px -1px 4px 2px rgba(0, 0, 0, 0.15);
        .tab {
          width: 25%;
          height: 80%;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 1.125em;
          font-weight: 500;
          &.active svg {
            // width: 45px;
            // height: 45px;
            // border-radius: 15px;
            transition: fill 0.25s;
            // background-color: $color1;
            fill: $color3;
          }
          div {
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            img {
              width: 25px;
              height: 25px;
              object-fit: cover;
            }
            svg {
            }
          }
        }
      }
    }
  }
}
@import "_Media_style";
